import React, { useEffect, useState } from 'react'
import { Button, Col, Container, Form, InputGroup, Row } from 'react-bootstrap'
import axios from 'axios'
import { message } from 'antd'

const Index = () => {
  const [search, setSearch] = useState('')
  const [user, setUser] = useState([])
  const [searchedUser, setSearchedUser] = useState(false)

  const handleSearch = async (e) => {
    try {
      e.preventDefault()
      e.stopPropagation()
      if (search === '') {
        message.error('Please enter UniqueId')
        return
      }
      const res = await axios.post(`/api/v1/masterAdmin/searchUser`, { search })
      if (res.data.success) {
        setUser(res.data.data)
        setSearchedUser(true)
      }
    } catch (error) {
      console.log(error)
      message.error(error.response.data.message)
    }
  }

  return (
    <>
      <Row className="mb-3">
        <Form className="d-flex justify-content-end" onClick={(e) => handleSearch(e)}>
          <Form.Group>
            <InputGroup>
              <Form.Control
                type="text"
                placeholder="Enter UniqueId"
                onChange={(e) => setSearch(e.target.value)}
              />
              <Button type="submit" className="myCustomBtn">
                Search
              </Button>
            </InputGroup>
          </Form.Group>
        </Form>
      </Row>
      {searchedUser && (
        <Row className="mb-3">
          <Container fluid className="p-5">
            <Row className="mb-3">
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formName"
              >
                <Form.Label>Name</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter Name"
                  value={user.name}
                  required
                  disabled
                />
              </Form.Group>
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formMobileNumber"
              >
                <Form.Label>WhatsApp Number</Form.Label>
                <Form.Control
                  type="number"
                  placeholder="Enter Mobile Number"
                  value={user.mobileNumber}
                  disabled
                />
              </Form.Group>
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formMobileNumber"
              >
                <Form.Label>Role</Form.Label>
                <Form.Control type="text" placeholder="Enter Role" value={user.role} disabled />
              </Form.Group>
            </Row>
            <Row className="mb-3">
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formName"
              >
                <Form.Label>Email</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Email"
                  value={user.email}
                  required
                  disabled
                />
              </Form.Group>
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formMobileNumber"
              >
                <Form.Label>Created By</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Created By"
                  disabled
                  value={user.createdBy ? user.createdBy : 'Owner'}
                />
              </Form.Group>
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formMobileNumber"
              >
                <Form.Label>Role</Form.Label>
                <Form.Control type="text" placeholder="PAN No." disabled value={user.role} />
              </Form.Group>
            </Row>
            <Row className="mb-3">
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formMobileNumber"
              >
                <Form.Label>Status</Form.Label>
                <Form.Control type="text" placeholder="PAN No." disabled value={user.status} />
              </Form.Group>
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formMobileNumber"
              >
                <Form.Label>PAN No.</Form.Label>
                <Form.Control type="text" placeholder="PAN No." disabled value={user.panCard} />
              </Form.Group>
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formMobileNumber"
              >
                <Form.Label>Aadhar No.</Form.Label>
                <Form.Control type="text" placeholder="PAN No." disabled value={user.aadharCard} />
              </Form.Group>
            </Row>
            <Row className="mb-3">
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formName"
              >
                <Form.Label>Unique ID</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Unique ID"
                  value={user.uniqueId}
                  required
                  disabled
                />
              </Form.Group>
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formMobileNumber"
              >
                <Form.Label>Aadhar Card</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Aadhar Card"
                  value={user.aadharCard}
                  required
                  disabled
                />
              </Form.Group>
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formMobileNumber"
              >
                <Form.Label>Total Partner Created</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Total Partner Created"
                  value={user.partners.length}
                  required
                  disabled
                />
              </Form.Group>
            </Row>
            <Row className="mb-3">
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formName"
              >
                <Form.Label>State</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="State"
                  value={user.state}
                  required
                  disabled
                />
              </Form.Group>
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formMobileNumber"
              >
                <Form.Label>City</Form.Label>
                <Form.Control type="text" placeholder="City" disabled value={user.city} />
              </Form.Group>
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formMobileNumber"
              >
                <Form.Label>Pin Code</Form.Label>
                <Form.Control type="text" placeholder="Pin code" disabled value={user.pinCode} />
              </Form.Group>
            </Row>
          </Container>
        </Row>
      )}
    </>
  )
}

export default Index
